@import "../node_modules/tinper-bee-core/scss/minxin-variables";
@import "../node_modules/tinper-bee-core/scss/minxin-mixins";
@import "../node_modules/bee-select/src/Select.scss";


$treePrefixCls: 'u-select-tree';
$selectPrefixCls: 'u-select';
//select样式
@mixin effect() {
  animation-duration: .3s;
  animation-fill-mode: both;
  transform-origin: 0 0;
}

.#{$selectPrefixCls} {
  box-sizing: border-box;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  color: #666;
  &:focus {
    outline: none;
  }
  &-allow-clear {
    .#{$selectPrefixCls}-selection--single .#{$selectPrefixCls}-selection__rendered {
      padding-right: 40px;
    }
  }

  ul, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  > ul > li > a {
    padding: 0;
    background-color: #fff;
  }

  &-selection {
    outline: none;
    &:hover .u-select-selection__clear {
      opacity: 1;
    }

    &__clear {
      position: absolute;
      top: 50%;
      right: 8px;
      z-index: 1;
      display: inline-block;
      margin-top: -6px;
      color: rgba(0,0,0,0.25);
      font-size: 12px;
      font-style: normal;
      line-height: 12px;
      text-align: center;
      text-transform: none;
      background: #fff;
      cursor: pointer;
      opacity: 0;
      -webkit-transition: color .3s ease,opacity .15s ease;
      transition: color .3s ease,opacity .15s ease;
      text-rendering: auto;
      .uf {
        padding: 0;
      }
      &:hover {
        color: rgba(0,0,0,0.45);
      }
    }

    &__placeholder {
      position: absolute;
      top: 50%;
      left: 5px;
      right: 9px;
      color: #909090;
      line-height: 20px;
      height: 20px;
      max-width: 100%;
      margin-top: -10px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  &-enabled &-selection {
    &:hover {
      border-color: #40a9ff;
      box-shadow: 0 0 2px fadeout(#40a9ff, 20%);
    }
    &:active {
      border-color: #40a9ff;
    }
  }

  &-selection--single {
    cursor: pointer;
    position: relative;

    .#{$selectPrefixCls}-selection__rendered {
      display: block;
      margin-left: 2px;
      margin-right: 2px;
      position: relative;
      line-height: 30px;
      height: 30px;
      .#{$selectPrefixCls}-selection__placeholder {
        position: absolute;
        top: 50%;
        left: 5px;
        right: 9px;
        color: #ccc;
        line-height: 20px;
        height: 20px;
        max-width: 100%;
        margin-top: -10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }

    .#{$selectPrefixCls}-selection-selected-value {
      float: left;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      max-width: 100%;
      padding-right: 14px;
      padding-left: 5px;
    }

  }

  &-disabled {
    color: #909090;
    cursor: not-allowed;

    .#{$selectPrefixCls}-selection--single,
    .#{$selectPrefixCls}-selection__choice__remove {
      cursor: not-allowed;
      color: #909090;

      &:hover {
        cursor: not-allowed;
        color: #909090;
      }
    }
  }

  &-sm {
    .u-select-selection--single {
      .u-select-selection__rendered {
        height: 24px;
        .u-select-selection-selected-value {
          line-height: 24px;
        }
      }
    }
  }
  &-lg {
    .u-select-selection--single {
      .u-select-selection__rendered {
        height: 38px;
        .u-select-selection-selected-value {
          line-height: 38px;
        }
      }
    }
  }

  &-search__field__wrap {
    display: inline-block;
    position: relative;
  }

  &-search__field__placeholder {
    position: absolute;
    top: 0;
    left: 3px;
    color: #aaa;
  }

  &-search__field__mirror {
    position: absolute;
    top: 0;
    left: -9999px;
    white-space: pre;
    pointer-events: none;
  }

  &-search--inline {
    float: left;
    width: 100%;
    .#{$selectPrefixCls}-search__field__wrap {
      width: 100%;
    }
    .#{$selectPrefixCls}-search__field {
      border: none;
      font-size: 100%;
      //margin-top: 5px;
      background: transparent;
      outline: 0;
      width: 100%;
    }
    > i {
      float: right;
    }
  }

  &-enabled.#{$selectPrefixCls}-selection--multiple {
    cursor: text;
  }

  &-selection--multiple {
    min-height: 28px;

    .#{$selectPrefixCls}-search--inline {
      width: auto;
      .#{$selectPrefixCls}-search__field {
        width: 0.75em;
      }
    }

    .#{$selectPrefixCls}-search__field__placeholder {
      top: 5px;
      left: 8px;
    }

    .#{$selectPrefixCls}-selection__rendered {
      //display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      padding-left: 8px;
      // padding-bottom: 2px;
      padding-right: 10px;
      line-height: 30px;
      min-height: 30px;
    }

    > ul > li {
      margin-top: 4px;
      height: 20px;
      line-height: 20px;
    }

    .#{$selectPrefixCls}-selection__clear {
       top: 5px;
       right: 8px;
     }
  }

  & &-selection__choice {
    background-color: #f3f3f3;
    border-radius: $border-radius-base;
    float: left;
    padding: 0 20px 0 10px;
    margin-right: 4px;
    position: relative;
    overflow: hidden;
    transition: padding .3s cubic-bezier(0.6, -0.28, 0.735, 0.045), width .3s cubic-bezier(0.6, -0.28, 0.735, 0.045);

    &__content {
      margin-left: 0;
      margin-right: 0;
      transition: margin .3s cubic-bezier(0.165, 0.84, 0.44, 1);
    }

    &-zoom-enter, &-zoom-appear, &-zoom-leave {
      @include effect();
      opacity: 0;
      animation-play-state: paused;
      animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
    }

    &-zoom-leave {
      opacity: 1;
      animation-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
    }

    &-zoom-enter.#{$selectPrefixCls}-selection__choice-zoom-enter-active,
    &-zoom-appear.#{$selectPrefixCls}-selection__choice-zoom-appear-active {
      animation-play-state: running;
      animation-name: rcSelectChoiceZoomIn;
    }

    &-zoom-leave.#{$selectPrefixCls}-selection__choice-zoom-leave-active {
      animation-play-state: running;
      animation-name: rcSelectChoiceZoomOut;
    }

    @keyframes rcSelectChoiceZoomIn {
      0% {
        transform: scale(0.6);
        opacity: 0;
      }
      100% {
        transform: scale(1);
        opacity: 1;
      }
    }

    @keyframes rcSelectChoiceZoomOut {
      to {
        transform: scale(0);
        opacity: 0;
      }
    }

    &__remove {
      color: #919191;
      cursor: pointer;
      font-weight: bold;
      padding: 0 0 0 8px;
      position: absolute;
      opacity: 1;
      transform: scale(1);
      top: 0;
      right: 4px;
      transition: opacity .3s, transform .3s;
      .uf {
        padding: 0;
        font-size: 12px;
      }

      &:hover {
        color: #333;
      }
    }
  }

  &-dropdown {

    &-menu {
      outline: none;
      margin: 0;
      padding: 0;
      list-style: none;
      z-index: 9999;

      > li {
        margin: 0;
        padding: 0;
      }

      &-item-group-list {
        margin: 0;
        padding: 0;

        > li.#{$selectPrefixCls}-menu-item {
          padding-left: 20px;
        }
      }

      &-item-group-title {
        color: #999;
        line-height: 1.5;
        padding: 8px 10px;
        border-bottom: 1px solid #dedede;
      }

      li {
       & &-item{
          margin: 0;
          position: relative;
          display: block;
          padding: 7px 10px;
          font-weight: normal;
          color: #666666;
          white-space: nowrap;
          &-selected {
            background-color: #ddd;
          }

          &-active {
            background-color: #5897fb;
            color: white;
            cursor: pointer;
          }

          &-disabled {
            color: #ccc;
            cursor: not-allowed;
          }

          &-divider {
            height: 1px;
            margin: 1px 0;
            overflow: hidden;
            background-color: #e5e5e5;
            line-height: 0;
          }
        }



      }
    }

    &-slide-up-enter, &-slide-up-appear {
      @include effect();
      opacity: 0;
      animation-timing-function: cubic-bezier(0.08, 0.82, 0.17, 1);
      animation-play-state: paused;
    }

    &-slide-up-leave {
      @include effect();
      opacity: 1;
      animation-timing-function: cubic-bezier(0.6, 0.04, 0.98, 0.34);
      animation-play-state: paused;
    }

    .#{$selectPrefixCls}-dropdown-slide-up-enter.#{$selectPrefixCls}-dropdown-slide-up-enter-active.#{$selectPrefixCls}-dropdown-placement-bottomLeft, .#{$selectPrefixCls}-dropdown-slide-up-appear.#{$selectPrefixCls}-dropdown-slide-up-appear-active.#{$selectPrefixCls}-dropdown-placement-bottomLeft {
      animation-name: rcSelectDropdownSlideUpIn;
      animation-play-state: running;
    }

    .#{$selectPrefixCls}-dropdown-slide-up-leave.#{$selectPrefixCls}-dropdown-slide-up-leave-active.#{$selectPrefixCls}-dropdown-placement-bottomLeft {
      animation-name: rcSelectDropdownSlideUpOut;
      animation-play-state: running;
    }

    .#{$selectPrefixCls}-dropdown-slide-up-enter.#{$selectPrefixCls}-dropdown-slide-up-enter-active.#{$selectPrefixCls}-dropdown-placement-topLeft, .#{$selectPrefixCls}-dropdown-slide-up-appear.#{$selectPrefixCls}-dropdown-slide-up-appear-active.#{$selectPrefixCls}-dropdown-placement-topLeft {
      animation-name: rcSelectDropdownSlideDownIn;
      animation-play-state: running;
    }

    .#{$selectPrefixCls}-dropdown-slide-up-leave.#{$selectPrefixCls}-dropdown-slide-up-leave-active.#{$selectPrefixCls}-dropdown-placement-topLeft {
      animation-name: rcSelectDropdownSlideDownOut;
      animation-play-state: running;
    }

    @keyframes rcSelectDropdownSlideUpIn {
      0% {
        opacity: 0;
        transform-origin: 0% 0%;
        transform: scaleY(0);
      }
      100% {
        opacity: 1;
        transform-origin: 0% 0%;
        transform: scaleY(1);
      }
    }
    @keyframes rcSelectDropdownSlideUpOut {
      0% {
        opacity: 1;
        transform-origin: 0% 0%;
        transform: scaleY(1);
      }
      100% {
        opacity: 0;
        transform-origin: 0% 0%;
        transform: scaleY(0);
      }
    }

    @keyframes rcSelectDropdownSlideDownIn {
      0% {
        opacity: 0;
        transform-origin: 0% 100%;
        transform: scaleY(0);
      }
      100% {
        opacity: 1;
        transform-origin: 0% 100%;
        transform: scaleY(1);
      }
    }
    @keyframes rcSelectDropdownSlideDownOut {
      0% {
        opacity: 1;
        transform-origin: 0% 100%;
        transform: scaleY(1);
      }
      100% {
        opacity: 0;
        transform-origin: 0% 100%;
        transform: scaleY(0);
      }
    }
  }

  &-dropdown-search {
    display: block;
    padding: 4px;
    .#{$selectPrefixCls}-search__field__wrap {
      width: 100%;
    }
    .#{$selectPrefixCls}-search__field__placeholder {
      top: 4px;
    }
    .#{$selectPrefixCls}-search__field {
      padding: 4px;
      width: 100%;
      box-sizing: border-box;
      border: 1px solid $border-color-base;
      border-radius: $border-radius-base;
      outline: none;
    }
    &.#{$selectPrefixCls}-search--hide {
      display: none;
    }
  }

  &-open {
    .#{$selectPrefixCls}-arrow b {
      border-color: transparent transparent #888 transparent;
      border-width: 0 4px 5px 4px;
    }
  }

  &-not-found {
    display: inline-block;
    padding: 8px;
  }
}

//tree节点样式
.#{$treePrefixCls} {
  margin: 0;
  padding: 5px;
  li {
    padding: 0;
    margin: 2px 0;
    list-style: none;
    white-space: nowrap;
    outline: 0;
    .#{$treePrefixCls}-title{
      line-height: 18px;
      cursor: pointer;
    }
    a[draggable],
    a[draggable="true"] {
      color: #333;
      -moz-user-select: none;
      -khtml-user-select: none;
      -webkit-user-select: none;
      user-select: none;
      /* Required to make elements draggable in old WebKit */
      -khtml-user-drag: element;
      -webkit-user-drag: element;
    }
    &.drag-over {
      > a[draggable] {
        background-color: #316ac5;
        color: white;
        border: 1px #316ac5 solid;
        opacity: 0.8;
      }
    }
    &.drag-over-gap-top {
      > a[draggable] {
        border-top: 2px blue solid;
      }
    }
    &.drag-over-gap-bottom {
      > a[draggable] {
        border-bottom: 2px blue solid;
      }
    }
    &.filter-node {
      > a {
        color: #a60000!important;
        font-weight: bold!important;
      }
      span.#{$treePrefixCls}-node-content-wrapper{
        color: #f50;
      }
    }
    span.#{$treePrefixCls}-node-content-wrapper {
      width: calc(100% - 48px);
      display: inline-block;
      padding: 3px 4px;
      border-radius: 2px;
      margin: 0;
      cursor: pointer;
      text-decoration: none;
      vertical-align: middle;
      color: $font-color-base;
      transition: all 0.3s ease;
      &:hover {
        background: $hover-bg-color-base;
      }
    }
    ul {
      margin: 0;
      padding: 0 0 0 18px;
      &.#{$treePrefixCls}-line {
        background: url("data:image/gif;base64,R0lGODlhCQACAIAAAMzMzP///yH5BAEAAAEALAAAAAAJAAIAAAIEjI9pUAA7") 0 0 repeat-y;
      }
    }
    a {
      display: inline-block;
      padding: 1px 3px 0 0;
      margin: 0;
      cursor: pointer;
      height: 17px;
      text-decoration: none;
      vertical-align: top;
    }
    span {
      &.#{$treePrefixCls}-switcher,
      &.#{$treePrefixCls}-checkbox,
      &.#{$treePrefixCls}-iconEle {
        line-height: 22px;
        margin-right: 2px;
        width: 22px;
        height: 22px;
        display: inline-block;
        vertical-align: middle;
        border: 0 none;
        cursor: pointer;
        outline: none;
        background-color: transparent;
        background-repeat: no-repeat;
        background-attachment: scroll;
        background-image: url("data:image/png;base64,");
      }
      &.#{$treePrefixCls}-icon_loading {
        margin-right: 2px;
        vertical-align: top;
        background: url("data:image/gif;base64,") no-repeat scroll 0 0 transparent;
      }
      &.#{$treePrefixCls}-switcher {
        padding-left: 5px;
        &-noop {
          cursor: auto;
          background: none;
        }
        &_open {
          // background-position: -93px -56px;
          background: #fff;
          &:before {
            font-family: 'uf';
            content: "\e639";
          }
        }
        &_close {
          // background-position: -75px -56px;
          background: #fff;
          &:before {
            font-family: 'uf';
            content: "\e61c";
          }
        }
      }
      &.#{$treePrefixCls}-checkbox {
        white-space: nowrap;
        cursor: pointer;
        outline: none;
        display: inline-block;
        line-height: 1;
        position: relative;
        vertical-align: middle;
        height: 16px;
        background: #fff;
      }
      &.#{$treePrefixCls}-checkbox-inner {
          position: relative;
          top: 0;
          left: 0;
          display: inline-block;
          width: 16px;
          height: 16px;
          border: 1px solid $border-color-base;
          border-radius: 3px;
          background-color: #fff;
          -webkit-transition: all .3s;
          transition: all .3s;
          box-sizing: border-box;
      }
      &.#{$treePrefixCls}-checkbox-disabled .#{$treePrefixCls}-checkbox-inner {
          border-color: #dfe1e6;
          background-color: #fff;
      }
      &.#{$treePrefixCls}-checkbox-indeterminate .#{$treePrefixCls}-checkbox-inner:after {
          content: ' ';
          -webkit-transform: scale(1);
          -ms-transform: scale(1);
          transform: scale(1);
          position: absolute;
          left: 2px;
          top: 5px;
          width: 8px;
          height: 1px;
      }
      &.#{$treePrefixCls}-checkbox-disabled.#{$treePrefixCls}-checkbox-checked .#{$treePrefixCls}-checkbox-inner:after {
          -webkit-animation-name: none;
          animation-name: none;
          border-color: #c1c7d0;
      }
      &.#{$treePrefixCls}-checkbox-disabled .#{$treePrefixCls}-checkbox-inner:after {
          -webkit-animation-name: none;
          animation-name: none;
          border-color: #c1c7d0;
      }
      &.#{$treePrefixCls}-checkbox-checked .#{$treePrefixCls}-checkbox-inner:after {
          -webkit-transform: rotate(45deg) scale(1);
          -ms-transform: rotate(45deg) scale(1);
          transform: rotate(45deg) scale(1);
          position: absolute;
          left: 4px;
          top: 1px;
          display: table;
          width: 5px;
          height: 8px;
          border: 2px solid $tree-checkbox-color;
          border-top: 0;
          border-left: 0;
          content: ' ';
          -webkit-transition: all 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
          transition: all 0.2s cubic-bezier(0.12, 0.4, 0.29, 1.46) 0.1s;
      }
      &.#{$treePrefixCls}-checkbox-inner:after {
          -webkit-transform: rotate(45deg) scale(0);
          -ms-transform: rotate(45deg) scale(0);
          transform: rotate(45deg) scale(0);
          position: absolute;
          left: 4px;
          top: 1px;
          display: table;
          width: 5px;
          height: 8px;
          border: 2px solid $tree-checkbox-color;
          border-top: 0;
          border-left: 0;
          content: ' ';
          -webkit-transition: all 0.1s cubic-bezier(0.71, -0.46, 0.88, 0.6);
          transition: all 0.1s cubic-bezier(0.71, -0.46, 0.88, 0.6);
      }
      li span.#{$treePrefixCls}-checkbox {
        margin: 2px 4px 0 0;
      }
    }
  }
  &-child-tree {
    display: none;
    &-open {
      display: block;
    }
  }
  &-treenode-disabled {
    >span,
    >a,
    >a span {
      color: #ccc;
      cursor: not-allowed;
    }
  }
  &-node-selected {
    display: inline-block;
    width: calc(100% - 40px);
    padding: 3px 4px;
    background-color: $selected-bg-color-base;
    // border: 1px #ffb951 solid;
    // opacity: 0.8;
  }
  &-icon__open {
    margin-right: 2px;
    background-position: -110px -16px;
    vertical-align: top;
  }
  &-icon__close {
    margin-right: 2px;
    background-position: -110px 0;
    vertical-align: top;
  }
}
